.menu {
  box-sizing: border-box;

  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;

  overflow: auto;

  &__top {
    margin-bottom: 32px;
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #c2c2c2;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 27px;
    color: $menu-text-color;
    text-decoration: none;
  }

  &__book-a-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $menu-text-color;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: $menu-text-color;
    text-decoration: none;
  }
}
